<template>
    <el-card>
        <template #header>{{ title }}</template>
        <el-alert>AOP é¢å‘åˆ‡ç‰‡ç¼–ç¨‹</el-alert>
        <highlightjs language="JavaScript" lineNumbers :autodetect="false" :code="code"></highlightjs>
    </el-card>
</template>

<script lang="ts" setup>
import { defineProps, nextTick } from 'vue'
defineProps({
    title: String
})
const code = `
class Circle {
    constructor() {
        this.border = 'none'
    }
    draw() {
        console.log('draw Circle', this)
    }
}

class Client {
    constructor(circle) {
        this.circle = circle
    }
    // æ·»åŠ æ–°åŠŸèƒ½
    draw() {
        this.circle.draw()
        this.setBorder(this.circle)
        this.circle.draw()
    }
    setBorder(circle) {
        circle.border = '1px'
        console.log('setBorder', circle)
    }
}
new Client(new Circle()).draw()
    `
nextTick(() => {
    eval(code)
})
</script>
