import { createRouter, createWebHistory } from 'vue-router'

// è·¯ç”±é›†åˆ
const routes = [
    {
        path: '/',
        name: 'Home',
        title: 'é¦–é¡µ',
        component: () => import('@/views/Home/index.vue'),
        meta: {
            name: 'Home',
            title: 'é¦–é¡µ'
        }
    }
]

// åŒ¹é…åˆ°çš„æ–‡ä»¶é»˜è®¤æ˜¯æ‡’åŠ è½½çš„ï¼Œé€šè¿‡åŠ¨æ€å¯¼å…¥å®ç°ï¼Œå¹¶ä¼šåœ¨æ„å»ºæ—¶åˆ†ç¦»ä¸ºç‹¬ç«‹çš„ chunkã€‚å¦‚æœä½ å€¾å‘äºç›´æ¥å¼•å…¥æ‰€æœ‰çš„æ¨¡å—ï¼ˆä¾‹å¦‚ä¾èµ–äºè¿™äº›æ¨¡å—ä¸­çš„å‰¯ä½œç”¨é¦–å…ˆè¢«åº”ç”¨ï¼‰ï¼Œä½ å¯ä»¥ä¼ å…¥ { eager: true } ä½œä¸ºç¬¬äºŒä¸ªå‚æ•°ï¼š
const views = import.meta.glob(`@/views/*/index.vue`, { eager: true })

// åŠ¨æ€åŠ è½½è·¯ç”±
for (const componentPath in views) {
    const module: any = views[componentPath]
    const match: Array<any> = componentPath.match(/..\/views\/(\w+)/) || []
    /// åŒ¹é…åˆ°çš„è·¯ç”±åç§°
    const routerName = match[1]
    // æ‰¾åˆ°exampleçš„ç»„ä»¶ï¼Œå¹¶åŠ è½½
    const $component = module.default
    // é»˜è®¤é¦–é¡µå¿…é¡»å¾—
    if (routerName && routerName !== 'Home' && !$component.hidden) {
        const routerTitle = $component.title
        const title = routerTitle || routerName
        routes.push({
            path: routerName === 'Home' ? '/' : `/${routerName}`,
            name: routerName,
            title,
            component: $component,
            meta: {
                name: routerName,
                title
            }
        })
    }
}

const router = createRouter({
    history: createWebHistory(import.meta.env.BASE_URL),
    routes: [...routes]
})

export const menus = routes.map((item: any) => {
    delete item.component
    return {
        ...item
    }
})

export default router
