interface ILog {
    capsule: (title: any, info: any, type: string) => void
    colorful: (textArr: any[]) => void
    default: (text: any) => void
    primary: (text: any) => void
    success: (text: any) => void
    warning: (text: any) => void
    info: (text: any) => void
    danger: (text: any) => void
}

export const log: any = {}

/**
 * @description è¿”å›è¿™ä¸ªæ ·å¼çš„é¢œè‰²å€¼
 * @param {String} type æ ·å¼åç§° [ default | primary | success | warning | info | danger ]
 * åŒelement-plusæŒ‰é’®ä¸»é¢˜
 * https://element-plus.org/zh-CN/guide/theming.html#%E9%80%9A%E8%BF%87-scss-%E5%8F%98%E9%87%8F
 */
function typeColor(type = 'default') {
    let color = ''
    switch (type) {
        case 'default':
            color = '#515a6e'
            break
        case 'primary':
            color = '#409eff'
            break
        case 'success':
            color = '#67c23a'
            break
        case 'warning':
            color = '#e6a23c'
            break
        case 'info':
            color = '#909399'
            break
        case 'danger':
            color = '#f56c6c'
            break
        default:
            break
    }
    return color
}

/**
 * @description æ‰“å°ä¸€ä¸ª [ title | text ] æ ·å¼çš„ä¿¡æ¯
 * @param {String} title title text
 * @param {String} info info text
 * @param {String} type style
 */
log.capsule = function (title: any, info: any, type = 'primary') {
    console.log(
        `%c ${title} %c ${info} %c`,
        'background:#35495E; padding: 1px; border-radius: 3px 0 0 3px; color: #fff;',
        `background:${typeColor(type)}; padding: 1px; border-radius: 0 3px 3px 0;  color: #fff;`,
        'background:transparent'
    )
}

/**
 * @description æ‰“å°å½©è‰²æ–‡å­—
 */
log.colorful = function (textArr: any[]) {
    console.log(
        `%c${textArr.map(t => JSON.stringify(t.text || '')).join('%c')}`,
        ...textArr.map(t => `color: ${typeColor(t.type)};`)
    )
}

/**
 * @description æ‰“å° default æ ·å¼çš„æ–‡å­—
 */
log.default = function (text: any) {
    log.colorful([{ text }])
}

/**
 * @description æ‰“å° primary æ ·å¼çš„æ–‡å­—
 */
log.primary = function (text: any) {
    log.colorful([{ text, type: 'primary' }])
}

/**
 * @description æ‰“å° success æ ·å¼çš„æ–‡å­—
 */
log.success = function (text: any) {
    log.colorful([{ text, type: 'success' }])
}

/**
 * @description æ‰“å° warning æ ·å¼çš„æ–‡å­—
 */
log.warning = function (text: any) {
    log.colorful([{ text, type: 'warning' }])
}

/**
 * @description æ‰“å° info æ ·å¼çš„æ–‡å­—
 */
log.info = function (text: any) {
    log.colorful([{ text, type: 'info' }])
}

/**
 * @description æ‰“å° danger æ ·å¼çš„æ–‡å­—
 */
log.danger = function (text: any) {
    log.colorful([{ text, type: 'danger' }])
}

export default log
