/**
 * ajax
 */
export default (params: any) => {
    return new Promise((resolve, reject) => {
        const { method = 'get', url, data } = params
        const xhr = new XMLHttpRequest()

        xhr.open(method, url, true)
        xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded') // ç®€å•è¯·æ±‚
        // xhr.setRequestHeader('Authorization', 'application/json; charset=utf-8'); // å¤æ‚è¯·æ±‚
        xhr.onreadystatechange = function () {
            /**
             * readyState
             * 0ï¼šè¿˜æ²¡è°ƒç”¨
             * 1ï¼šè½½å…¥ï¼ˆæ­£åœ¨å‘é€ï¼‰
             * 2ï¼šè½½å…¥å®Œæˆ
             * 3ï¼šè§£æå“åº”å†…å®¹
             * 4ï¼šè§£æå®Œæˆ
             */
            if (xhr.readyState === 4) {
                /**
                 * httpçŠ¶æ€ç 
                 * 1XX çŠ¶æ€æŒ‡ç¤º
                 * 2XX æˆåŠŸè¿”å›
                 * 3XX é‡å®šå‘
                 * 4XX å®¢æˆ·ç«¯é”™è¯¯
                 * 5XX æœåŠ¡ç«¯é”™è¯¯
                 */
                if (xhr.status === 200) {
                    resolve(JSON.parse(xhr.responseText))
                } else {
                    reject(xhr)
                }
            }
        }
        xhr.send(JSON.stringify(data) || null)
    })
}
