import axios from 'axios'
import type { AxiosInstance, AxiosRequestConfig } from 'axios'
import { log } from '@/plugin/index'

export interface IResponse {
    code?: number
    message?: string
    time?: string | number
    data?: any
}

class Request {
    // axioså®ä¾‹
    instance: AxiosInstance
    // ç”¨äºå­˜å‚¨æ§åˆ¶å™¨å¯¹è±¡
    abortControllerMap: Map<string, AbortController>
    // æ„é€ å‡½æ•°
    constructor(config?: AxiosRequestConfig) {
        // åˆ›å»ºaxioså®ä¾‹
        this.instance = axios.create({
            baseURL: '/api',
            ...config
        })
        // ç”¨äºå­˜å‚¨æ§åˆ¶å™¨å¯¹è±¡
        this.abortControllerMap = new Map()
        // è®¾ç½®æ‹¦æˆªå™¨
        this.setInterceptors(this.instance)
    }
    // è¯·æ±‚
    request(config: AxiosRequestConfig): Promise<IResponse> {
        return this.instance(config)
    }
    // æ‹¦æˆªå™¨
    setInterceptors(request: AxiosInstance) {
        // è¯·æ±‚æ‹¦æˆªå™¨
        request.interceptors.request.use(config => {
            // toDo ä¹Ÿå¯ä»¥åœ¨è¿™é‡Œåšä¸€ä¸ªé‡å¤è¯·æ±‚çš„æ‹¦æˆª
            // https://github.com/axios/axios/tree/main#abortcontroller
            // è¯·æ±‚urlä¸ºkey
            const url = config.url || ''
            // å®ä¾‹åŒ–æ§åˆ¶å™¨
            const controller = new AbortController()
            // å°†æ§åˆ¶å™¨å®ä¾‹ä¸è¯·æ±‚ç»‘å®š
            config.signal = controller.signal
            // å°†æ§åˆ¶å™¨å®ä¾‹å­˜å‚¨åˆ°Mapä¸­
            this.abortControllerMap.set(url, controller)
            // è®¾ç½®è¯·æ±‚å¤´
            config.headers = {
                ...config.headers,
                'x-auth-token': import.meta.env.VITE_TOKEN
            }
            return config
        })
        // å“åº”æ‹¦æˆªå™¨
        request.interceptors.response.use(res => {
            const url = res.config.url || ''
            // è¯·æ±‚å®Œæˆåï¼Œå°†æ§åˆ¶å™¨å®ä¾‹ä»Mapä¸­ç§»é™¤
            this.abortControllerMap.delete(url)
            if (axios.isCancel(res)) {
                console.log('Request canceled', res)
                return Promise.reject(res)
            }
            if (res.status === 200) {
                return Promise.resolve(res.data)
            }
            log.danger(res)
            return Promise.reject(res)
        })
    }
    /**
     * å–æ¶ˆå…¨éƒ¨è¯·æ±‚
     */
    cancelAllRequest() {
        for (const [, controller] of this.abortControllerMap) {
            // å–æ¶ˆè¯·æ±‚
            controller.abort()
        }
        this.abortControllerMap.clear()
    }
    /**
     * å–æ¶ˆæŒ‡å®šçš„è¯·æ±‚
     * @param url å¾…å–æ¶ˆçš„è¯·æ±‚URL
     */
    cancelRequest(url: string | string[]) {
        const urlList = Array.isArray(url) ? url : [url]
        for (const _url of urlList) {
            // å–æ¶ˆè¯·æ±‚
            this.abortControllerMap.get(_url)?.abort()
            this.abortControllerMap.delete(_url)
        }
    }
}

export default new Request()
